<template>
    <div id="Policy">
        <link rel="stylesheet" href="https://unpkg.com/tootik@1.0.2/css/tootik.min.css">
        <div class="navigation">
            <div class="menuToggle"></div>
            <ul>
                <li class="list active" data-tootik="home" data-tootik-conf="right default" id="home">
                    <a style="--clr:#f44336;">
                        <span class="icon">
                            <MdHomeIcon />
                        </span>
                        <span class="text">游戏主页</span>
                    </a>
                </li>
                <li class="list" data-tootik="about" data-tootik-conf="right default" id="about">
                    <a style="--clr:#ffa117;">
                        <span class="icon">
                            <MdBookIcon />
                        </span>
                        <span class="text">隐私条例</span>
                    </a>
                </li>
                <li class="list" data-tootik="our" data-tootik-conf="right default" id="contact">
                    <a style="--clr:#0fc70f;">

                        <span class="icon">
                            <MdPersonIcon />
                        </span>
                        <span class="text">联系方式</span>
                    </a>
                </li>
                <div class="indicator"></div>
            </ul>
        </div>
        <!-- <div id="container"> -->
        <router-view></router-view>
        <!-- </div> -->

    </div>
</template>
  
<script>
import MdHomeIcon from 'vue-ionicons/dist/md-home.vue'
import MdPersonIcon from 'vue-ionicons/dist/md-person.vue'
import MdBookIcon from 'vue-ionicons/dist/md-book.vue'

export default {
    name: 'App',

    components: {
        MdHomeIcon,
        MdPersonIcon,
        MdBookIcon,
    },

    mounted() {


        const menuToggle = document.querySelector('.menuToggle');
        const navigation = document.querySelector('.navigation');
        menuToggle.onclick = function () {
            navigation.classList.toggle('active')
        }

        const _router = this.$router
        const list = document.querySelectorAll('.list');
        function activeLink() {
            list.forEach((item) =>
                item.classList.remove('active'));
            this.classList.add('active')

            if (this.id == 'home') {
                console.log("home")
            } else if (this.id == 'about') {
                console.log("about")
            } else if (this.id == 'our') {
                console.log("our")
            }
            _router.push({ name: this.id })

            console.log("点击了", this.id, _router)
        }

        list.forEach((item) =>
            item.addEventListener('click', activeLink));
    }

}

</script>
  
